import React from "react";
import { useAudio } from "../../hooks/useAudio";
import { CircleMicSvg } from "../../svg/CircleMicSvg";
import { DisconnectedMicrophoneSvg } from "../../svg/DisconnectedMicrophoneSvg";
import { NoNetIcon } from "../../svg/NotNetIcon";
import {
  Div1,
  Div2,
  BgDefault,
  BgListening,
  BgSpeaking,
  BgIntermediate,
  FinanbroBtnRoot,
  FinanbroBtnSC,
  RecognizedTextContent,
  RecognizedTextHolder,
  BgUnderstood,
  DisconnectedMicIcon,
  LowVolumeIcon,
  Icon,
  IconWrapper,
  TriangleMicIconBg,
  GiSpeakerIcon,
  NotNetSC,
  VscDebugStartIcon,
  SiProbotIcon,
} from "./finanbroBtnSC";
export const FinanbroBtn = ({
  onClick,
  isListening,
  isSpeaking,
  transcript,
}) => {
  const [playing, toggle] = useAudio(
    "./audio/zapsplat_multimedia_button_click_007_53868.mp3"
  );

  return (
    <FinanbroBtnRoot
      id="FinanbroBtnRoot"
      onClick={() => {
        toggle();
        onClick();
      }}
    >
      <RecognizedTextHolder id="RecognizedTextHolder">
        <RecognizedTextContent
          id="RecognizedTextContent"
          hasTranscript={transcript.length > 0}
        >
          {transcript}
        </RecognizedTextContent>
      </RecognizedTextHolder>
      <FinanbroBtnSC
        id="FinanbroBtnSC"
        isListening={isListening}
        isSpeaking={isSpeaking}
      >
        <Div1 id="Div1" />
        <Div2 id="Div2" />
        <BgDefault id="BgDefault" />
        <BgListening
          id="BgListening"
          isListening={isListening}
        />
        <BgSpeaking id="BgSpeaking" isSpeaking={isSpeaking} />
        <BgIntermediate id="BgIntermediate" />
        <BgUnderstood id="BgUnderstood" />
        <IconWrapper>
          {isListening ? (
            <Icon id="app" src="images/mic.png" />
          ) : isSpeaking ? (
            <GiSpeakerIcon />
          ) : (
            <SiProbotIcon />
          )}
        </IconWrapper>
        <TriangleMicIconBg />
        <CircleMicSvg />
        <DisconnectedMicIcon
          id="DisconnectedMicIcon"
          src="data:image/svg+xml;base64,"
          alt=""
        />
        <LowVolumeIcon src="data:image/svg+xml;base64," />
        {/* <NoNetIcon /> */}
      </FinanbroBtnSC>
    </FinanbroBtnRoot>
  );
};
